.hero {
  height: 500px;
  width: 100%;
  background: #eee;
  display: flex; }
  .hero .hero-container {
    width: 1024px;
    height: 450px;
    margin: auto;
    display: flex;
    flex-direction: column; }
    .hero .hero-container .hero-contents {
      flex: 5;
      display: flex;
      flex-direction: column;
      font-family: "Noto Sans KR", sans-serif; }
      .hero .hero-container .hero-contents .hero-text {
        margin: auto;
        width: 50%;
        text-align: center;
        display: flex;
        flex-direction: column; }
        .hero .hero-container .hero-contents .hero-text h1 {
          align-content: flex-end;
          color: #333;
          font-size: 2em;
          font-weight: 500; }
        .hero .hero-container .hero-contents .hero-text p {
          align-content: center;
          color: #666;
          font-size: 1em;
          font-weight: 400; }
      .hero .hero-container .hero-contents .hero-button {
        margin: auto;
        width: 50%;
        display: flex;
        justify-content: center; }
        .hero .hero-container .hero-contents .hero-button * {
          margin: auto 0.5em; }
    .hero .hero-container .hero-search {
      flex: 1;
      margin-top: 50px; }

.hero-card-container {
  flex: 1;
  margin: 0.7em;
  display: flex;
  justify-content: center; }
  .hero-card-container * {
    margin: 0 0.5em; }
